const buttonsOpenMenu = document.querySelectorAll('.js-menu-open');
const buttonsCloseMenu = document.querySelectorAll('.js-close-menu');
const $mainBannerBurger = document.querySelector('.s-main-banner__burger-menu');
const $mainMenu = document.querySelector('#main-menu');
buttonsOpenMenu.forEach($button => {
	$button.addEventListener('click', (e) => {
		e.preventDefault();
		$mainBannerBurger.classList.toggle('is-active');
		$mainMenu.classList.toggle('is-open');
	})
});
buttonsCloseMenu.forEach($button => {
	$button.addEventListener('click', (e) => {
		e.preventDefault();
		$mainBannerBurger.classList.remove('is-active');
		$mainMenu.classList.remove('is-open');
	})
});

document.addEventListener('click', (e) => {
	if (!e.target.closest('.s-main-menu') &&
		!e.target.closest('.js-menu-open')) {
		$mainBannerBurger.classList.remove('is-active');
		$mainMenu.classList.remove('is-open');
	}
});
