const map = document.querySelector('#contacts_map');
if (map) {
	const center = [59.928194, 30.346644];
	const script = document.createElement('script');
	document.body.appendChild(script);
	script.src = 'https://api-maps.yandex.ru/2.1/?lang=ru_RU&apikey=e1c2b193-edfc-446f-b034-3c6048d70037';

	const init = () => {
		const myMap = new ymaps.Map(map, {
			center: center,
			zoom: 15,
			controls: []
		});
		myMap.behaviors.disable('scrollZoom');

		let myPlacemark = new ymaps.Placemark(center, { hintContent: ' '}, {
			iconLayout: 'default#image',
			iconImageHref: 'images/map-img5.svg',
			iconImageSize: [77, 98],
			iconImageOffset: [-33.5, -98]
		});

		myMap.geoObjects
			.add(myPlacemark)
	}

	script.onload = () => {
		ymaps.ready(function () { init() });
	};
}
