const map = document.querySelector('#b-map__map');
if (map) {
	const center = [59.928194, 30.346644];
	const script = document.createElement('script');
	document.body.appendChild(script);
	script.src = 'https://api-maps.yandex.ru/2.1/?lang=ru_RU&apikey=e1c2b193-edfc-446f-b034-3c6048d70037';

	const init = () => {
		const myMap = new ymaps.Map(map, {
			center: center,
			zoom: 15,
			controls: []
		});
		myMap.behaviors.disable('scrollZoom');

		let myPlacemark = new ymaps.Placemark(center, { hintContent: ' '}, {
			iconLayout: 'default#image',
			iconImageHref: 'images/map-img5.svg',
			iconImageSize: [77, 98],
			iconImageOffset: [-33.5, -98]
		});

		myMap.geoObjects
			.add(myPlacemark)

		window.myObjects = ymaps.geoQuery({
			type: "FeatureCollection",
			features: [
				{
					type: 'Feature',
					geometry: {
						type: 'Point',
						coordinates:[59.931194, 30.345644]
					},
					options: {
						type: 'Образование',
						iconLayout: 'default#image',
						iconImageHref: 'images/map-img6.svg',
						iconImageSize: [50, 64],
						iconImageOffset: [-25, -64]
					}
				},
				{
					type: 'Feature',
					geometry: {
						type: 'Point',
						coordinates: [59.931194, 30.342644]
					},
					options: {
						type: 'Транспорт',
						iconLayout: 'default#image',
						iconImageHref: 'images/map-img4.svg',
						iconImageSize: [50, 64],
						iconImageOffset: [-25, -64]
					}
				},
				{
					type: 'Feature',
					geometry: {
						type: 'Point',
						coordinates: [59.923194, 30.344644]
					},
					options: {
						type: 'Досуг',
						iconLayout: 'default#image',
						iconImageHref: 'images/map-img3.svg',
						iconImageSize: [50, 64],
						iconImageOffset: [-25, -64]
					}
				},
				{
					type: 'Feature',
					geometry: {
						type: 'Point',
						coordinates: [59.928194, 30.334644]
					},
					options: {
						type: 'Магазины',
						iconLayout: 'default#image',
						iconImageHref: 'images/map-img2.svg',
						iconImageSize: [50, 64],
						iconImageOffset: [-25, -64]
					}
				},
				{
					type: 'Feature',
					geometry: {
						type: 'Point',
						coordinates: [59.928194, 30.356644]
					},
					options: {
						type: 'Медицина',
						iconLayout: 'default#image',
						iconImageHref: 'images/map-img1.svg',
						iconImageSize: [50, 64],
						iconImageOffset: [-25, -64]
					}
				},
				{
					type: 'Feature',
					geometry: {
						type: 'Point',
						coordinates: [59.928194, 30.353644]
					},
					options: {
						type: 'Медицина',
						iconLayout: 'default#image',
						iconImageHref: 'images/map-img1.svg',
						iconImageSize: [50, 64],
						iconImageOffset: [-25, -64]
					}
				}
			]
		}).addToMap(myMap);

		const lnks = document.querySelectorAll('.b-map__lnk');

		lnks.forEach($lnk => {
			$lnk.addEventListener('click', (e) => {
				e.preventDefault();
				$lnk.closest('.b-map__itm').classList.toggle('is-active');
				showGeo();
			})
		});

		const showGeo = () => {
			let shownObjects,
				geoGroup = new ymaps.GeoQueryResult();
			const lnksActive = document.querySelectorAll('.b-map__itm.is-active .b-map__lnk');
			if (!lnksActive.length) {
				geoGroup = myObjects.search(`options.type != ""`)
					.add(geoGroup);
				shownObjects = geoGroup.addToMap(myMap);
				return;
			}
			lnksActive.forEach($lnk => {
				let search = $lnk.getAttribute('href')
				geoGroup = myObjects.search(`options.type = "${search}"`)
					.add(geoGroup);
				shownObjects = geoGroup.addToMap(myMap);
				myObjects.remove(shownObjects).removeFromMap(myMap);
			});
		}
		showGeo();

	}

	script.onload = () => {
		ymaps.ready(function () { init() });
	};
}
