import Swiper, { Navigation, Pagination, Autoplay } from 'swiper';
const slider = new Swiper('.b-carousel__swiper', {
	modules: [Navigation, Pagination, Autoplay],
	slidesPerView: 1.2,
	spaceBetween: 10,
	loop: true,
	navigation: {
		nextEl: '.b-carousel__swiper-button-next',
		prevEl: '.b-carousel__swiper-button-prev'
	},
	breakpoints: {
		320: {
			slidesPerView: 1.2,
			spaceBetween: 10
		},
		768: {
			slidesPerView: 3.2,
			spaceBetween: 30
		}
	}
});

